import chanpinyaosu from "@/components/productShow/chanpinyaosu-show.vue"
import machineStatus from "@/components/productShow/machineStatus.vue"
import uniIcon from "@/components/uni-icons/uni-icons.vue";
let _this
export default {
	components:{
		chanpinyaosu,
		machineStatus,
		uniIcon
	},
	data() {
		return {
			goodsCode:'',
			goodsDetail:''
		};
	},
	onShow:function(){
		this.getGoodsDetail()
	},
	onLoad:function(options){
		_this = this
		this.goodsCode = options.goodsCode
		this.getGoodsDetail()
	},
	methods:{	
		getGoodsDetail:function(){
			let url = _this.Api.goodsDetails
			let data ={
				goodsCode: _this.goodsCode,  // 产品编码 
			}
			uni.showLoading({
				title:'提交中',
				mask: true
			})
			this.myRequest(data,url,'get').then(res => {
			      console.log(res)
				  if(res.data.status==0){
					  uni.hideLoading()
					  _this.goodsDetail = res.data.data
					  console.log(_this.goodsDetail)
				  }
				  
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		toBigPic:function(src){
			
			// this.src = src
			// this.$refs.bigimage.show()
			uni.previewImage({
				urls: [src],
				success:function(){
					console.log('正在预览中')
				},
				fail:function(){
					
				},
				complete:function(){
					
				}
			})
			
		},
		bindCancel:function(){
			let url = _this.Api.auditPass
			let data ={
				goodsCode: _this.goodsCode,  // 产品编码 
			}
			uni.showLoading({
				title:'提交中',
				mask: true
			})
			this.myRequest(data,url,'get').then(res => {
				  uni.hideLoading()
			      console.log(res)
				  if(res.data.status==0){
					  uni.showToast({
					  	title: '审核成功',
						duration: 1000
					  });
					  _this.getGoodsDetail()
					 
				  }
				  
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		submit:function(){
			let data = JSON.stringify(_this.goodsDetail)
			uni.navigateTo({
				url: './auditProduct?goodsDetail='+ data,
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		}
	}
}
